import { useRef, useState } from 'react';
import lang from './localization'
import './App.scss';

function App() {
  const elRef = useRef()
  const elTitle = useRef()
  const elTitleFooter = useRef()
  const elParagraph = useRef()
  const [language, setLang] = useState("uz")

  // ! | Style Elements
  const body = document.querySelector("body")
  // ! | Style Elements
    return (
      <>
        <header>
          <div className="container">
            <h1 ref={elTitle}>{lang[language].title}</h1>
            <select ref={elRef} onChange={ (e) => {
              if(e.target.value === "uz"){
                setLang("uz")
              }
              else if(e.target.value === "ru"){
                setLang("ru")
              }
              else if(e.target.value === "en"){
                setLang("en")
              }
            }}>
              <option value="uz">uz</option>
              <option value="ru">ru</option>
              <option value="en">en</option>
            </select>
          </div>
        </header>
        <main>
          <div className="container">
            <h3 ref={elParagraph}>{lang[language].paragraph}</h3>
          </div>
        </main>
        <footer>
            <div className="container">
              <h1 ref={elTitleFooter}>Footer</h1>
              <button onClick = { () => {
                body.style.backgroundColor = "rgb(0, 10, 32)"
                body.style.color = "#fff"
                elTitle.current.style.color = "gold"
                elTitleFooter.current.style.color = "gold"
                elParagraph.current.style.color = "#fff"
              }}>Dark</button>
              <button onClick = { () => {
                body.style.backgroundColor = "#fff"
                body.style.color = "#333"
                elTitle.current.style.color = "coral"
                elTitleFooter.current.style.color = "coral"
                elParagraph.current.style.color = "#000"
              }}>Light</button>
            </div>
        </footer>
      </>
    );
}

export default App;
